var SnippetChooser = /** @class */ (function () {
    function SnippetChooser(html, idPattern, model) {
        this.html = html;
        this.idPattern = idPattern;
        this.model = model;
    }
    SnippetChooser.prototype.render = function (placeholder, name, id, initialState) {
        var html = this.html.replace(/__NAME__/g, name).replace(/__ID__/g, id);
        // eslint-disable-next-line no-param-reassign
        placeholder.outerHTML = html;
        /* the chooser object returned by createImageChooser also serves as the JS widget representation */
        // eslint-disable-next-line no-undef
        var chooser = createSnippetChooser(id, this.model);
        chooser.setState(initialState);
        return chooser;
    };
    return SnippetChooser;
}());
window.telepath.register('wagtail.snippets.widgets.SnippetChooser', SnippetChooser);
